@import '_styles';

.work-title {
  font-size: 1.25rem;
  font-weight: 600;
}

.work-image:hover {
  padding-left: 0.25rem;
  border-left: 0.25rem solid $text;
}

.work-grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 1rem;
}

// mobile layout
.work-image {
  height: 212px;
  width: 376px;
  border: 1px solid rgba(154, 153, 153, 0.8);
  box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
  border-radius: 1rem;
  background-size: cover;
  padding: 0.5rem;
  border-left: 0 solid $text;
  transition: border-left 0.3 ease-in-out, padding-left 0.3 ease-in-out;
  cursor: pointer;
}

.details-work {
  list-style-type: none;
  li {
    .li-title {
      background-color: $secondary20;
      border-radius: 0.25rem;
      padding: 1px 5px;
      color: $text;
    }
  }
}

// tablet and larger layout
@media screen and (min-device-width: 750px) {
  .work-image {
    height: 300px;
    width: 534px;
    border: 1px solid rgba(154, 153, 153, 0.8);
    box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
    border-radius: 1rem;
    background-size: cover;
    padding: 0.5rem;
    border-left: 0 solid $text;
    transition: border-left 0.3 ease-in-out, padding-left 0.3 ease-in-out;
    cursor: pointer;
  }
}
